@import "./../../info-page.scss";

.reset-password-page {
  .body-contacts{
    background-color: #FBFBFB;
    padding-bottom: 90px;
  }
  .footer{
    padding-top: 44px;
  }
  .current-page-info {
    .current-page-info {
      &__container {
        .current-page-info__page-name {
          color: $gray;
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
    }
  }
  .reset-password {
    &__container {
      display: flex;
      align-items: center;
      flex-direction: column;
      padding-top: 109px;
      padding-bottom: 100px;
      @media (max-width: $mobile) {
        padding-top: 80px;
        padding-bottom: 45px;
      }
      .reset-password__title {
        @include rale-800-32;
        margin-bottom: 24px;
        letter-spacing: 0.01em;
        word-spacing: 0.04em;
        text-align: center;
      }
      .blocks-reset-password {
        display: flex;
        flex-direction: column;
        .block-reset-password {
          display: flex;
          align-items: center;
          flex-direction: column;
          &__title {
            @include rale-600-18;
            .icon-lock {
              font-size: 19px;
              margin-right: 11px;
              color: $darkGreen;
            }
            .icon-unlock {
              font-size: 19px;
              margin-right: 11px;
              color: $darkGreen;
            }
          }
          &__description {
            margin-top: 10px;
            letter-spacing: 0.02em;
            color: $gray;
            @include rale-500-15;
            max-width: 410px;
            display: block;
            text-align: center;
            line-height: 20px;
          }
          &__input {
            border: 0.5px solid;
            border-color: rgba($color: #000000, $alpha: 0.15);
            padding: 17px 20px 12px 10px;
            max-width: 420px;
            max-height: 47px;
            width: 100%;
            height: 100%;
            font-family: Montserrat;
            font-size: 14px;
            font-weight: 600;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.21;
            letter-spacing: normal;
            text-align: left;
            color: #1a2f3f;
            &.email {
              margin-top: 17px;
            }
          }
          &:nth-child(2) {
            margin-top: 50px;
            .block-reset-password__description {
              text-align: left;
              max-width: none;
              width: 100%;
              letter-spacing: 0.15px;
              @media (max-width: $mobile) {
                text-align: center;
              }
            }
            .button.rectangle {
              margin-top: 15px;
              padding: 15px 49px 12px 45px;
            }
            .block-reset-password__description {
              text-align: start;
            }
            .block-reset-password__input {
              padding: 14px 20px 12px 10px;
            }
          }
        }
        .button.rectangle.background {
          margin-top: 16px;
          width: 245px;
          padding: 14px 45px 12px 47px;
          text-align: center;
          @include rale-700-16;
          letter-spacing: 0;
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
    }
  }
}
